          _arr1Set = new Set(arr1),
          _arr2Set = new Set(arr2);
        if (arr2.length == 0) return true;
        //    arr1.filter((item)=>{
        //    console.log(_arr2Set.has(item))
        //  })

        //has()  DOM 
        return arr1.filter((item) => _arr2Set.has(item)).length > 0;
      });
