          );

          this.filterInfo(g3);
          // console.log('true', g3)
        }
      } else {
        this.getFlightList();
        console.log("else");
      }
    },
    getFlightList () {
      // this.upload();
      this.isOpen = -1;
      let _this = this;
      let datex = this.search.date[this.now_trip == "0" ? 0 : 1];
