      //
      let that = this;

      if (this.AssociationList.length == 0) {
        let g = this.Association;
        this.AssociationList = g[0].remark;
      }
      //
      this.v_12306_title = "12306";
      that.v_s.login = true;
      that.s12306ve1 = false;
      that.s12306ve3 = false;
      that.s12306ve4 = false;
      that.smsget = "";

      that.stutas1();
    },

    async isRegisterNext () {
      let that = this;
      // let v_s = this.v_s;
      if (!this.formx.user || !this.formx.pass) return;

      sessionStorage.setItem("formx12306", JSON.stringify(this.formx));
      console.log(that.v_s.status);
      let formd = that.formx;
      if (that.v_s.status == -2) {
        this.s12306ve4 = false; //15
        this.v_12306_title = "";
        that.v_s.status = 0;
      } else if (that.v_s.status == -1) {
        //
        that.v_s.status = 2;
        that.s12306ve3 = false;
        that.v_s.login = false;
        that.check_123_error = "";
        that.v_12306_title = "";
      } else if (that.v_s.status == 0) {
        this.get_acccount(that.AssociationList, function () {
          that.v_s.status = 1;
          // that.stutas1();
          that.s12306ve4 = false;
          that.stutas1();
        });
      } else if (that.v_s.status == 1) {
        //
        if (that.v_s.error_num[formd.user] == 4) {
          this.messConfirm_ui(
            "4"
          );
          return;
        }
        // this.upload();

        await axios({
          method: "POST", //GET  POST
          url: this.apilistx[0] + "Apply12306JSON",
          dataType: "json", //
          data: JSON.stringify({
            account12306: {
              userName12306: formd.user,
              userPass12306: formd.pass,
            },
            requestKey: new GUID().newGUID(),
          }),
        })
          .then(
            (res) => {
              // this.$toast.clear();
              let ex = res.data;
              console.log(ex);
              if (ex.checkResultCode != 1) {
                if (ex.checkResultCode == 0 && ex.errorCode == 101) {
                  //
                  that.v_s.status = 1.5;

                  let pg = ex.checkResultMsg.split(/|/)[1];
                  that.v_s.sms_c = pg;
                  that.v_s.login = false;
                } else {
                  // console.log(ex.checkResultMsg)
                  that.messConfirm_ui(ex.checkResultMsg);
                }
                if (ex.checkResultMsg == "") {
                  if (that.v_s.error_num[formd.user])
                    that.v_s.error_num[formd.user]++;
                  else that.v_s.error_num[formd.user] = 1;

                  this.checkResultMsgErro =
                    ",4(" +
                    that.v_s.error_num[formd.user] +
                    ")";
                }
              } else {
                that.v_s.status = 3;
                that.v_s.login = false;
                that.check_123_success = "";
                that.s12306ve1 = true;
              }
              console.log(that.v_s);
            },
            (error) => {
              that.$toast.clear();
              console.log(error);
              that.messConfirm_ui("");
            }
          )
          .catch((e) => {
            // this.$toast.clear();
            that.messConfirm_ui("");
          });
      } else if (that.v_s.status == 1.4) {
        //
        let p = that.AssociationList;
        // that.upload();
