    async check_psg (formData) {
      // this.upload();

      let that = this;
      await axios({
        method: "POST",
        url: this.apilistx[0] + "query12306PassengerInfo",
        dataType: "json", //
        data: JSON.stringify({
          account12306Info: {
            userName12306: formData.user,
            userPass12306: formData.pass,
            requestKey: new GUID().newGUID(),
          },
        }),
      }).then(
        (res) => {
          let e = res.data;
          var g = JSON.parse(sessionStorage.getItem("book_train"));
          if (e.length + g.list.length <= 15) {
            this.check_psg2(true, formData);
          } else {
            let num = e.length + g.list.length;

            e.map((v, i) => {
              g.list.map((v2, i2) => {
                if (
                  v.certificateName.replace(/\//g, " ") ==
                  v2.key.FltPaxnameDom.replace(/\//g, " ") &&
                  v.certificateNo.substr(-3) == v2.key.IdNo.substr(-3)
                ) {
                  num--;
                }
              });
            });
            if (num <= 15) {
              this.check_psg2(true, formData);
            } else {
              this.check_psg2(false, formData);
            }
          }
        },
        (error) => {
          // that.$toast.clear();
          console.log(error);
          that.messConfirm_ui("");
        }
      );
      // .catch((e) => {
      //   this.$toast.clear();
