    },
    async save12306 (fn, formData) {
      let that = this;
      let p = that.AssociationList;
      if (p == "" || (that.new_booker.by && p == "")) {
        // that.upload();
        let formd = formData;
        var profileid = that.booker.Identification;
        if (that.new_booker.by && p == "") {
          profileid = that.new_booker.psgx.approvestafffre[0].Identification;
        }

        await axios({
          method: "POST",
          url: that.apilistx[0] + "SaveBooker12306Account",
          dataType: "text",
          data: JSON.stringify({
            username: formd.user,
            password: formd.pass,
            profileid: profileid,
            requestKey: new GUID().newGUID(),
          }),
        })
          .then(
            (e) => {
              // that.$toast.clear();
              if (fn) {
                //try 
                fn();
              }
            },
            (error) => {
              // that.$toast.clear();
              that.messConfirm_ui("");
            }
          );
      } else {
        fn();
      }
