    async book_train (allstr) {
      // this.upload();
      await axios({
        method: "POST", //GET  POST
        url: "https://porsche.efasco.com/ErpService.asmx/SavePorscheTrainTrips", //,
        contentType: "application/x-www-form-urlencoded;charset=UTF-8", //,UTF-8"application/x-www-form-urlencoded"
        dataType: "json", //
        data: allstr, //JSON.stringify(allstr),
      })
        .then((res) => {
          // this.$toast.clear();
          this.Traveler_popup = false;
          this.pupRelevanceShow = false;

          var result = res.data;
          console.log(result, result.toString().indexOf("error"));
          if (result.toString().indexOf("error") < 0) {
            console.log(
              "result--->",
              result
            );
            allstr = JSON.stringify(result); //allstr  ;
            this.sussTime = result.tktdeadline;
            this.submt_info = result;
            this.successBookShow = true;

            sessionStorage.removeItem("choose_train");
          } else if (result.toString().indexOf("") < 0) {
            this.messConfirm_ui("/Booking failed");
          } else {
            this.messConfirm_ui("/Booking failed");
          }
        })
        .catch((err) => {
          // this.$toast.clear();
          this.messConfirm_ui("/Booking failed");
          console.log(err);
        });
    },
    get_submit_data () {
      let result = {};
      let a = this.$store.state.CltId_set;
      let guid = sessionStorage.getItem("guid");
      if (!guid) {
        guid = new GUID().newGUID();
        // console.log(guid);
      }
      result.tktdeadline = "2018-05-29 18:00:00";
      result.timespan = guid;
      // result.guid = guid;
      result.staffno = this.booker.StaffNo;
      // result.name = this.booker.FltPaxnameDom;
      result.source = "app";
      result.list = [];
      result.paymentmethod = this.paymenValueObj.chtext;
      result.travelpurpose = this.purposeValueObj.chtext;
      // result.returnUrl =
      result.accountname = "";

      this.search_train.psg.map((v, i) => {
        let ga = [];

        v.IdCardValidDate = "";
        v.IdNo = crypto.decrypt(v.IdNo);
        v.IdCardNo = v.IdNo;

        v.HkmcEepNo = this.get_null_tru(v.HkmcEepNo);
        v.PassportNo = this.get_null_tru(v.PassportNo);
        v.MtpNo = this.get_null_tru(v.MtpNo);
        v.ReentryPermitNo = this.get_null_tru(v.ReentryPermitNo);
        v.OtherIdNo = this.get_null_tru(v.OtherIdNo);

        // v.idtype='';

        let Sex = null;
        if (v.Sex == "M") v.n_s = v.FltPaxnameDom + "/" + "";
        else v.n_s = v.FltPaxnameDom + "/" + "";

        this.choose_train.map((v2, i2) => {
          console.log(v2);
          let dep = this.search_train.cities[i2 == 0 ? 0 : 1];
          let arr = this.search_train.cities[i2 == 0 ? 1 : 0];
          let ca = v2.cabin; //JSON.parse(JSON.stringify(v2.cabin));
          ca.Seat_position = this.c_s[i2][i] ? "TD" + this.c_s[i2][i][1] : null;
          ca.Seat_positionname = this.c_s[i2][i] || null;
          (ca.cccode = this.costRadioList ? this.costRadioList : ""),
            (ca.ccid = ""); //  ca.ccid=41200

          ca.inordercode = "";
          ca.inorderid = "";
          ca.rccode = "";

          if (
            ca.Seat_name == "" ||
            ca.Seat_name == "" ||
            ca.Seat_name == ""
          )
            ca.rccode = this.costRadioList;
          let vc = {
            aboutTrain: v2.seg,
            abuoutSet: ca,
            traincity: {
              departure: {
                cities: dep.namecn,
                name_en: dep.nameen,
                title: dep.nameen[0],
              },
              arrival: {
                cities: arr.namecn,
                name_en: arr.nameen,
                title: arr.nameen[0],
              },
              depdate: this.search_train.date[i2].replace(/-/g, ""),
            },
            psg: v,
          };
          ga.push(vc);
        });
        result.list.push({
          key: v,
          items: ga,
        });
      });
      // console.log(result);
      return result;
    },
