    getDay (a) {
      return new Date(a).getMonth() + 1 + "" + new Date(a).getDate() + "";
    },
    getWeek (a) {
      let v = ["", "", "", "", "", "", "", ""];
      return "" + v[new Date(a).getDay()];
    },
    enMonth (e) {
      var monthsInEng = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Spt",
        "Oct",
        "Nov",
        "Dec",
      ]; //
      return monthsInEng[new Date(e).getMonth()] + " " + new Date(e).getDate();
    },
    enDay (e) {
      var weekInEng = [
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
        "Sunday",
      ]; //
      return weekInEng[new Date(e).getDay()];
    },
    toMoney (num) {
      num = parseFloat(num);
      //console.log(num)
      //num = num.toFixed(2);
      //console.log(num)
      num = num + "";

      //console.log(num)
      if (num.indexOf(".") > 0) {
        var x = num.split(".")[1];
        var y = num.split(".")[0];
        if (x.length >= 2) {
          return y + "." + x.substr(0, 2);
        } else if (x.length == 1) {
          return num + "0";
        } else if (x.length == 0) {
          return num + "00";
        }
      } else {
        return num + ".00"; //23,245.122
      }
    },
  },
  components: {
    ScrollTop,
  },
};
